/** Variables **/
/** General **/
:root {
  --accent-color: $accentColor !important;
  --accent-color-hover: $accentColorHover !important;
  --accent-color-active: $accentcolorActive !important;
  --accent-rgb: $accentRgb !important ;
}

/** Fonts **/
* {
  font-family: ubuntu, sans-serif !important;
}

.markup-2BOw-j pre,
.markup-2BOw-j pre *,
.markup-2BOw-j pre code,
.markup-2BOw-j pre code *,
.markup-2BOw-j code.inline {
  font-family: "Operator Mono", monospace !important;
}

.ace_editor::before {
  font-family: "Operator Mono", monospace !important;
}